:root{
    --dark : #374151;
    --darker: #1F2937;
    --darkest : #111827;
    --gray : #6B7280;
    --light : #EEE;
    --pink : #EC4899;
    --purple : #8B5cf6;
}

* {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Fira sans', sans-serif;
}

body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background-color: var(--dark);
    color: #FFF;
}

/* Header */
header {
    padding: 2rem 1rem;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
    h1{
        font-size: 2.5rem;
        font-weight: 300;
        color: var(--gray);
        margin-bottom: 1rem;
    }
    #new-task-form {
        display: flex;
        #new-task-input {
            flex: 1 1 0%;
            background-color: var(--darker);
            padding: 1rem;
            border-radius: 1rem;
            margin-right: 1rem;
            color: var(--light);
            font-size: 1.25rem;
            &::placeholder{
                color: var(--gray);
            }
        }    
        #new-task-submit{
            color: var(--pink);
            font-size: 1.25rem; 
            font-weight: 700;
            cursor: pointer;
            transition: 0.4s;
            &:hover{
                opacity: 0.8;
            }
            &:active{
                opacity: 0.6;
            }            
        }
    }
}


input, button {
    appearance: none;
    border: none;
    outline: none;
    background: none;
}




/* Main */
main{
    flex: 1 1 0%;
    max-width: 800px;
    width: 100%;
    margin: 0 auto;
    .task-list {
        padding: 1rem;
        h2{
            font-size: 1.5rem;
            font-weight: 300;
            color: var(--gray);
            margin-bottom: 1rem;
        }
        #tasks .task{
            display: flex;
            justify-content: space-between;
            background-color: var(--darkest);
            padding: 1rem;
            border-radius: 1rem;
            margin-bottom: 1rem;
            .content{
                flex: 1 1 0%;
                color: var(--light);
                .text{
                    color: var(--light);
                    font-size: 1.125rem;
                    width: 100%;
                    display: block;
                    transition: 0.4s;
                    &:noy(:read-only) {
                        color:var(--pink)
                    }
                }
            }
        
            #tasks .task .action{
             display: flex;
             margin: 0 -0.5rem;
            }
        }
    }
}

.task .actions button{
    cursor: pointer;
    margin: 0 0.5rem;
    font-size: 1.125rem;
    font-weight: 700;
    text-transform: uppercase;
    transition: 0.8;
}
#new-task-button:hover{
    opacity: 0.8;
}
#new-task-button:active{
    opacity: 0.6;
}
.task .actions .edit{
    color: var(--pink);
}
.task .actions .delete{
    color:red
}